*{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}



.root{
    background-image: url("../../Images/coding.png");
    background-position: center;
    background-size: cover;
    height: 100vh;
}

.root h1{
    color: white;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 52px;
    text-align: center;
}

.arrow {
    color: white;
    position: absolute;
    left: 50%;
    top: 95%;
    transform: translate(-50%, 0%);
    font-size: xx-large;
}

.makeBig {
    width: 200px;
    height: 200px;
}

.below{
    text-align: center;
    padding: 20px;
    font-family: sans-serif;
}

.below p{
    padding-top: 5%;
    position: absolute;
    top: 118%;
    width: 45%;
}

.about{
    height: 30vh;
    width: 100%;;
    left: 50%;
    transform: translate(-50%, 0);
    background: #00B4DB;
    background: -webkit-linear-gradient(to right, #0083B0, #00B4DB); 
    background: linear-gradient(to right, #0083B0, #00B4DB);
    position: absolute;
    top: 100vh;
    padding-bottom: 110px;
}
.about p{
    position: absolute;
    top: 15%;
    width: 100%;
    text-align: center;
    color: #fff;
    width: 80%;
    margin: auto;
    padding-left: 10%;
}




.aboutH{
    padding: 25px;
    font-size: 50px;
    font-weight: 600;
    color: #fff;
}

.button{
    padding: 20px;
    position: relative;
}

.aboutP{
    padding: 10px;
    margin: 10px;
    color: white;
    font-size: 20px;
}

.image{
    margin: 0;
    padding: 0;
    position: absolute;
    left: 50%;
    width: 50%;
    height: 60vh;
    /* background-image: url('../../images/teamwork.jpg'); */
    background-repeat: no-repeat;
    margin-top: 85vh;
}

.below h1{
    padding-top: 20px;
    position: absolute;
    left: 25%;
    top: 105%;
    transform: translate(-50%, 0%);
    
}
